









































































































































































































































































































@import "../styles/base.scss";

.clear {
  clear: both;
}

@mixin focused () {
  outline: none;
  border-color: $color-border-active;
  border-color: rgba(0,0,0,0.48);
}

@mixin hovered () {
  border-color: $color-border-hover;
  border-color: rgba(0,0,0,0.24);
}

.wrapper {
  display: block;
  padding: 0;

  &_contrasted {
    color: $color-text-contrast;
  }
}

.label {
  display: block;
  width: 100%;
}

.counter {
  @include fontSize2();
  float: right;
  transition: color 0.5s;

  &_negative {
    color: $color-error;
  }
}

.input {
  width: 100%;
  box-sizing: border-box;
  border: 1px solid $color-border;
  border-radius: $border-radius;
  transition: border-color 0.5s;
  position: relative;
  background: $color-input-bg;

  &:hover {
    @include hovered();
  }

  &_focused, &_focused:hover {
    @include focused();
  }

  &_disabled {
    background: $color-bg-disabled;
  }

  &_error {
    border-color: $color-error !important;
  }

  &_norightround {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }

  &__table {
    display: table;
    width: 100%;
    box-sizing: border-box;
    border-spacing: 0px;
  }

  &__row {
    display: table-row;
  }

  &__part {
    display: table-cell;
    background-color: rgba(0, 0, 0, 0);
    cursor: text;
    color: $color-text;
    min-height: $x10;
    @include fontSize3();

    &_pre {
      padding: 0;
      padding-left: $x3;
      vertical-align: middle;
    }

    &_main {
      width: 100%;
      box-sizing: border-box;
      margin: 0;
      padding: 0;
    }
  }

  &__post {
    position: absolute;
    right: 0;
    padding-right: $x3;

    &_textarea {
      padding-right: $x5;
    }
  }

  &__under {
  }
}

input, textarea {
  width: 100%;
  box-sizing: border-box;
  appearance: none;
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
  outline: none;
  white-space: pre-wrap;
  overflow-wrap: break-word;
  padding: $x2 $x3;
  padding-right: $x3;
  vertical-align: top;

  @include fontSize3();
  &:disabled {
    background: rgba(0, 0, 0, 0);
  }
}

.title {
  margin-bottom: $x1;
  @include fontSize2();
  @include fontControlLabel();
  @media (max-device-width: $size-grid * $touch-size) {
    & {
      font-weight: $font-weight;
    }
  }

  .required {
    color: $color-error;
  }
}

.error {
  @include fontSize2();
  color: $color-error;
  margin-top: $x2;
}

.description {
  @include fontSize2();
  margin-top: $x2;
  color: $color-text-secondary;
}

.big {
  .title {
    @include fontSize4();
    font-weight: bold;
    @media (max-device-width: $size-grid * $touch-size) {
      & {
        font-weight: $font-weight;
      }
    }
  }

  .input {
    &__part {
      &_pre {
        @include fontSize4();
        padding: 0;
        padding-left: $x3;
      }

      &_main {
        input, textarea {
          border: 0;
          @include fontSize4();
        }

        padding: 0;
      }
    }
  }

  input, textarea {
    padding: $x3;
  }

  .error {
    @include fontSize3();
  }

  .description {
    @include fontSize2();
    margin-top: $x1;
  }
}

.touch {
  .title {
    @media (max-device-width: $size-grid * $touch-size) {
      & {
        font-weight: bold;
      }
    }
  }
  .input {
    &__part {
      &_pre {
        @include fontSize4();
        padding-left: $x3;
      }

      &_main {
        input, textarea {
          border: 0;
          @include fontSize4();
        }
      }
    }
  }

  input, textarea {
    padding: $x3;
  }

  .description {
    @include fontSize3();
  }
}

textarea {
  resize: vertical;

  &.autosize {
    resize: none;

    &_less {
      overflow-y: hidden;
    }
  }
}

