






































@import "./styles/base.scss";

body, html {
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
  min-width: 350px;
}

a {
  color: $color-link-secondary;
}

.wrapper {
  display: table;
  height: 100%;
  table-layout: fixed;
  width: 100%;
  box-sizing: border-box;

  & > * {
    display: table-row;

    & > * {
      display: table-cell;
    }
  }
}
