/**
 * Fade
 */
:global(.fade-enter-active), :global(.fade-leave-active) {
  transition: opacity .3s; }

:global(.fade-enter), :global(.fade-leave-to) {
  opacity: 0; }

.recover_form {
  max-width: 500px;
  margin: 20px auto; }

.header {
  font-size: 20px;
  line-height: 40px;
  font-weight: 400;
  font-family: inherit;
  text-transform: uppercase;
  margin-bottom: 24px; }
  .header span {
    display: block;
    text-transform: lowercase;
    font-size: 13px;
    line-height: 20px;
    font-weight: 400;
    font-family: inherit; }

.table {
  display: table;
  width: 100%; }
  .table__row {
    display: table-row; }
  .table__cell {
    display: table-cell;
    vertical-align: middle; }

.error, .success {
  margin-left: 20px;
  font-size: 13px;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit; }

.error {
  color: #bb3a3a; }

.success {
  color: #3abb70; }

.input, .captcha {
  margin-bottom: 16px; }
